<template>
  <div class="table">
    <el-table :data="dataList" border style="width: 100%" @sort-change="handleSortChange" height="600">
      <el-table-column fixed prop="id" label="ID" min-width="80" sortable="custom">
      </el-table-column>
      <el-table-column label="icon" width="80">
        <template slot-scope="scope">
          <img style="width: 50px;height:50px;margin:auto" :src="scope.row.iconUrl" alt="">
        </template>
      </el-table-column>
      <el-table-column label="" min-width="180">
        <template slot-scope="scope">
          <el-button type="text" @click="goDetailPage(scope.row)" v-if="!isCheckpage">{{scope.row.name}}</el-button>
          <span v-if="isCheckpage">{{scope.row.name}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="versionCode" label="" min-width="100">
      </el-table-column>
      <el-table-column prop="status" label="" min-width="120">
        <template slot-scope="scope">
          <!-- 0- 1- 2- 3- 4- 5- 6- -->
          <span v-if="scope.row.status===0" style="color:gray"></span>
          <span v-if="scope.row.status===1" style="color:Orange"></span>
          <span v-if="scope.row.status===2" style="color:Orange"></span>
          <span v-if="scope.row.status===3" style="color:Orange"></span>
          <span v-if="scope.row.status===4" style="color:green"></span>
          <span v-if="scope.row.status===5" style="color:black"></span>
          <span v-if="scope.row.status===6" style="color:red"></span>
        </template>
      </el-table-column>
      <el-table-column prop="startTime" label="" min-width="170" sortable="custom">
      </el-table-column>
      <el-table-column prop="endTime" label="" min-width="170" sortable="custom">
      </el-table-column>
      <el-table-column prop="modifier" label="" min-width="150">
      </el-table-column>
      <el-table-column label="" min-width="270">
        <template slot-scope="scope">
          <el-button v-if="scope.row.status===2" @click="shangjia(scope.row)" size="mini" type="success"></el-button>
          <el-button v-if="scope.row.status===4" @click="xiajia(scope.row)" size="mini" type="warning"></el-button>
          <el-button v-if="scope.row.status===4||scope.row.status===5" @click="jinyong(scope.row)" size="mini" type="info"></el-button>
          <el-button v-if="scope.row.status===0||scope.row.status===3||scope.row.status===5||scope.row.status===6" @click="goEditPage(scope.row)" size="mini" type="primary"></el-button>
          <el-button v-if="scope.row.status===0" @click="handleDelete(scope.row)" size="mini" type="danger"></el-button>
          <el-button @click="previewTest(scope.row)" size="mini" type="primary"></el-button>
          <!-- <el-button v-if="scope.row.status===4||scope.row.status===5||scope.row.status===6" @click="goDataPage(scope.row)" size="mini" type="primary"></el-button> -->
          <el-button v-if="isCheckpage&&scope.row.status===4" @click="goCheckPage(scope.row)" size="mini" type="primary"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="" :visible.sync="xiajiaDialogVisible" width="600px" :before-close="xiajiaDialogClose" center>
      <el-form :model="xiajiaData" ref="xiajiaData" label-width="100px">
        <el-form-item label="" prop="reason" :rules="applyRules">
          <el-input v-model="xiajiaData.reason" :autosize="{ minRows: 2, maxRows: 3}" type="textarea" placeholder="" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{xiajiaData.reason?xiajiaData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="xiajiaDialogClose(true)"> </el-button>
        <el-button type="primary" @click="xiajiaDialogClose()"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="IMEI" :visible.sync="testDialogVisible" width="600px" center>
      <div class="test-dialog">
        <span>1-20IMEI</span>
        <div style="display: flex;margin:20px">
        <el-input v-model="imeiTestValue" placeholder="IMEI" clearable style="width:200px;margin-right: 20px;"></el-input>
        <el-button type="primary" size="mini" @click="saveIMEI()"></el-button>
        </div>
        <div style="display:flex;width:100%;padding:10px 20px;flex-wrap: wrap">
          <el-tag v-for="(imei, index) in imeiList" :key="index" closable type="info" @close="deleteIMEI(index)" style="margin-right: 10px;margin-bottom: 10px">
            {{imei}}
          </el-tag>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="testDialogVisible=false"> </el-button>
        <el-button type="primary" @click="testBtn()"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="jinyongDialogVisible" width="600px" :before-close="jinyongDialogClose" center>
      <el-form :model="jinyongData" ref="jinyongData" label-width="100px">
        <el-form-item label=" " prop="reason" :rules="applyRules">
          <el-input v-model="jinyongData.reason" :autosize="{ minRows: 2, maxRows: 3}" type="textarea" placeholder="" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{jinyongData.reason?jinyongData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="jinyongDialogClose(true)"> </el-button>
        <el-button type="primary" @click="jinyongDialogClose()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "table-component",
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    },
    dataList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    var specialValidate = (rule, value, callback) => {
      if (/^\s+$/.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      testData: {},
      imeiTestValue: "",
      imeiList: [],
      testDialogVisible: false,
      jinyongDialogVisible: false,
      jinyongData: {},
      xiajiaDialogVisible: false,
      xiajiaData: {},
      applyRules: [
        { required: true, message: "", trigger: "blur" },
        { required: true, validator: specialValidate, trigger: "blur" }
      ]
    };
  },
  methods: {
    saveIMEI(){
      if (this.imeiList.length > 19) {
        this.$confirm("1-20IMEI", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
      } else if (this.imeiList.includes(this.imeiTestValue)) {
        this.$confirm("IMEI", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
      } else if (/^\d{15}$/.test(this.imeiTestValue)) {
        this.imeiList.push(this.imeiTestValue);
      } else {
        this.$confirm("IMEI(15)", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
      }
    },
    deleteIMEI(index) {
      this.imeiList.splice(index, 1);
    },
    testBtn() {
      if(this.imeiList.length<1){
        this.$confirm("IMEI", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
        return false
      }
      this.uploadLoading = true;
      let param = {
        status: this.testData.status,
        name: this.testData.name,
        startTime: this.testData.startTime,
        endTime: this.testData.endTime,
        priority: this.testData.priority,
        iconUrl: this.testData.iconUrl,
        displayText: this.testData.displayText,
        textLink: this.testData.textLink,
        buttonName: this.testData.buttonName,
        buttonLink: this.testData.buttonLink,
        notice: this.testData.notice,
        imeis: this.imeiList.join(';')
      };
      this.$http
        .post("/scene/previewTips", param)
        .then(response => {
          if(response.data.code===0){
            this.$message({
                  type: "success",
                  message: "!"
                });
            this.uploadLoading = false;
            this.testDialogVisible = false;
          }else {
            this.uploadLoading = false;
            this.testDialogVisible = false;
            this.$message.error('!');
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //
    handleDelete(data) {
      let message = "" + data.name + "";
      this.$confirm(message, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          let param = {
            tipsId: data.tipsId,
            versionCode: data.versionCode
          };
          this.$http
            .post("/scene/delTips", param)
            .then(response => {
              if (response.data && response.data.code == "0") {
                this.refreshData();
                this.$message({
                  type: "success",
                  message: "!"
                });
              } else {
                if (response.data.msg) {
                  this.$message.error(response.data.msg);
                } else {
                  this.$message.error("");
                }
              }
            })
            .catch(() => {
              this.$message.error("");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //
    goDataPage(data) {
      this.$emit("table-event", {
        type: "data-page",
        data: data
      });
    },
    //
    previewTest(row) {
      this.testData = row;
      this.testDialogVisible = true;
    },
    //
    goEditPage(data) {
      let endTime = Date.parse(data.endTime);
      if (data.status !== 0 && Date.now() > endTime) {
        this.$confirm(", ", "", {
          confirmButtonText: "",
          showCancelButton: false,
          type: "warning",
          center: true
        });
      } else {
        this.$emit("table-event", {
          type: "edit-page",
          data: data
        });
      }
    },
    //
    jinyongDialogClose(done) {
      if (typeof done === "function") {
        this.jinyongData = {};
        done();
      } else if (done) {
        this.jinyongData = {};
        this.jinyongDialogVisible = false;
      } else {
        this.$refs["jinyongData"].validate(valid => {
          if (valid) {
            let param = {
              tipsId: this.jinyongData.tipsId,
              versionCode: this.jinyongData.versionCode,
              disableReason: this.jinyongData.reason
            };
            this.$http
              .post("/scene/disableTips", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.refreshData();
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  this.xiajiaData = {};
                  this.jinyongDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("");
                  }
                }
              })
              .catch(() => {
                this.$message.error("");
              });
          } else {
            this.$message.warning("");
            return false;
          }
        });
      }
    },
    //
    jinyong(data) {
      this.jinyongData = data;
      this.jinyongDialogVisible = true;
    },
    //
    xiajiaDialogClose(done) {
      if (typeof done === "function") {
        this.xiajiaData = {};
        done();
      } else if (done) {
        this.xiajiaData = {};
        this.xiajiaDialogVisible = false;
      } else {
        this.$refs["xiajiaData"].validate(valid => {
          if (valid) {
            let param = {
              tipsId: this.xiajiaData.tipsId,
              versionCode: this.xiajiaData.versionCode,
              pulloffReason: this.xiajiaData.reason
            };
            this.$http
              .post("/scene/pulloffTips", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.refreshData();
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  this.xiajiaData = {};
                  this.xiajiaDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("");
                  }
                }
              })
              .catch(() => {
                this.$message.error("");
              });
          } else {
            this.$message.warning("");
            return false;
          }
        });
      }
    },
    //
    xiajia(data) {
      this.xiajiaData = data;
      this.xiajiaDialogVisible = true;
    },
    // shangjiaDialogClose(done) {
    //   if (typeof done === "function") {
    //     this.shangjiaData = {};
    //     done();
    //   } else if (done) {
    //     this.shangjiaData = {};
    //     this.shangjiaDialogVisible = false;
    //   } else {
    //   }
    // },
    //
    shangjia(data) {
      let param = {
        tipsId: data.tipsId,
        versionCode: data.versionCode,
      };
      this.$http
        .post("/scene/putawayTips", param)
        .then(response => {
          if (response.data && response.data.code == "0") {
            this.refreshData();
            this.$message({
              type: "success",
              message: ""
            });
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("");
            }
          }
        })
        .catch(() => {
          this.$message.error("");
        });
    },
    //
    goDetailPage(data) {
      this.$emit("table-event", {
        type: "detail-page",
        data: data
      });
    },
    //
    goCheckPage(data) {
      this.$emit("table-event", {
        type: "check-page",
        data: data
      });
    },
    //
    refreshData() {
      this.$emit("table-event", {
        type: "refresh-data"
      });
    },
    //
    handleSortChange(data) {
      let field = data.prop;
      let order = "";
      if (data.order === "ascending") {
        order = "asc";
      } else if (data.order === "descending") {
        order = "desc";
      }
      this.$emit("table-event", {
        type: "sortChange",
        data: {
          field: field,
          order: order
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.test-dialog {
  margin-top: -30px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.table {
  width: 100%;
  margin-top: 30px;
}
.shangjia-div {
  display: flex;
  flex-direction: column;
}
.text-line {
  display: flex;
  align-items: center;
}
.shangjia-date {
  margin-left: 7px;
}
.shangjia-text1 {
  color: rgb(192, 0, 0);
  margin-bottom: 20px;
}
.shangjia-text2 {
  font-size: 10px;
  color: rgb(196, 196, 196);
  margin-bottom: 20px;
}
</style>