    var specialValidate = (rule, value, callback) => {
      if (/^\s+$/.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateNumber = (rule, value, callback) => {
      if (!/^[0-9]*$/.test(value)) {
      callback(new Error(''));
      }
      if(value < 1 || value > 50) {
      callback(new Error('1~50'));
      }
      callback();
    };
