  methods: {
    saveIMEI(){
      if (this.imeiList.length > 19) {
        this.$confirm("1-20IMEI", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
      } else if (this.imeiList.includes(this.imeiTestValue)) {
        this.$confirm("IMEI", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
      } else if (/^\d{15}$/.test(this.imeiTestValue)) {
        this.imeiList.push(this.imeiTestValue);
      } else {
        this.$confirm("IMEI(15)", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
      }
    },
    deleteIMEI(index) {
      this.imeiList.splice(index, 1);
    },
    testBtn() {
      if(this.imeiList.length<1){
        this.$confirm("IMEI", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
        return false
      }
      this.uploadLoading = true;
      let param = {
        // tipsId: this.baseData.tipsId,
        status: this.baseData.status,
        // versionCode: this.baseData.versionCode,
        name: this.nameData.name,
        startTime: this.contentData.time[0],
        endTime: this.contentData.time[1],
        priority: this.contentData.priority,
        iconUrl: this.contentData.iconUrl,
        displayText: this.contentData.displayText,
        textLink: this.textLinkData,
        buttonName: this.btnDialogVisible?this.btnData.buttonName:'',
        buttonLink: this.btnDialogVisible?this.btnData:undefined,
        notice: this.noticeDialogVisible?this.noticeData:undefined,
        imeis: this.imeiList.join(';')
      };
      this.$http
        .post("/scene/previewTips", param)
        .then(response => {
          if(response.data.code===0){
            this.$message({
                  type: "success",
                  message: "!"
                });
            this.uploadLoading = false;
            this.testDialogVisible = false;
          }else {
            this.uploadLoading = false;
            this.testDialogVisible = false;
            this.$message.error('!');
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //icon
    handleIconSuccess(res, file) {
      this.contentData.iconUrl = URL.createObjectURL(file.raw);
    }, 
    beforeIconUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isPNG = file.type === 'image/png';
      const isLt50k = file.size / 1024 < 51;

      if (!isJPG&&!isPNG) {
        this.$message.error('');
      }
      if (!isLt50k) {
        this.$message.error('');
      }
      return (isJPG||isPNG) && isLt50k;
    },
    uploadIcon(data) {
      let param = new FormData();
      param.append("tipsId", 'tips_2');
      param.append("iconFile", data.file);
      this.uploadLoading = true;
      this.$http
        .post("/scene/uploadTipsIcon", param)
        .then(response => {
          if(response.data.code===0){
            this.contentData.iconUrl = response.data.data && response.data.data;
            this.contentData = JSON.parse(JSON.stringify(this.contentData))
            this.uploadLoading = false;
          }else {
            this.uploadLoading = false;
            this.$message.error(response.data.msg);
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },

    numberChange(value) {
      if (/^\d+$/.test(value)) {
        let number = Number(value);
        if (number >= 1 && number <= 10080) {
          this.isNoticeTimeError = false;
        } else {
          this.isNoticeTimeError = true;
        }
      } else {
        this.isNoticeTimeError = true;
      }
    },
    dateChange(isStart) {
      let startDate = Date.parse(this.contentData.time[0]);
      let endDate = Date.parse(this.contentData.time[1]);
      let now = Date.now();
      if (startDate < now) {
        this.isTimeError2 = true;
        this.isTimeError1 = false;
      } else if (endDate < now) {
        this.isTimeError2 = true;
        this.isTimeError1 = false;
      } else if (startDate >= endDate) {
        this.isTimeError1 = true;
        this.isTimeError2 = false;
      } else {
        this.isTimeError1 = false;
        this.isTimeError2 = false;
      }
    },
    noticeRadio2Choose() {
      this.noticeData.disappearTime = 120;
      this.noticeData = JSON.parse(JSON.stringify(this.noticeData))
    },
    //
    handleDelete() {
      console.log('shanchu')
      let message = "" + this.baseData.name + "";
      this.$confirm(message, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          let param = {
            tipsId: this.baseData.tipsId,
            versionCode: this.baseData.versionCode
          };
          this.$http
            .post("/scene/delTips", param)
            .then(response => {
              if (response.data && response.data.code == "0") {
                this.$message({
                  type: "success",
                  message: "!"
                });
                this.$emit("tips-event", {
                  type: "home-page"
                });
              } else {
                if (response.data.msg) {
                  this.$message.error(response.data.msg);
                } else {
                  this.$message.error("");
                }
              }
            })
            .catch(() => {
              this.$message.error("");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //
    xiajiaDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.xiajiaDialogVisible = false;
      } else {
        this.$refs["xiajiaData"].validate(valid => {
          if (valid) {
            let param = {
              tipsId: this.baseData.tipsId,
              versionCode: this.baseData.versionCode,
              pulloffReason: this.baseData.reason
            };
            this.$http
              .post("/scene/pulloffTips", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  this.xiajiaReason = this.baseData.reason;
                  this.baseData.reason = "";
                  this.baseData.status = 12;
                  this.xiajiaDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("");
                  }
                }
              })
              .catch(() => {
                this.$message.error("");
              });
          } else {
            this.$message.warning("");
            return false;
          }
        });
      }
    },
