    isEmpty(key) {
      if (typeof key === 'undefined' || key === '' || key === null) {
        return true;
      } else {
        return false;
      }
    },
    dataValidate (canEmpty) {
      let nflag, cflag, tflag, bflag, noflag = false;
      this.$refs['nameData']&&this.$refs['nameData'].validate((valid) => {
        nflag = valid
      });
      this.$refs['contentData'].validate((valid) => {
        cflag = valid
      });
      this.$refs['textLinkData'].validate((valid) => {
        tflag = valid
      });
      bflag = !this.btnDialogVisible;
      this.btnDialogVisible&&this.$refs['btnData'].validate((valid) => {
        bflag = valid
      });
      noflag = !this.noticeDialogVisible;
      this.noticeDialogVisible&&this.$refs['noticeData'].validate((valid) => {
        noflag = valid
      });
      // 
      if(this.textLinkData.linkType===2&&!(this.textLinkData.appPackageName||this.textLinkData.cpFastLink||this.textLinkData.cpH5Link)){
        this.$confirm("", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
        return false;
      }
      if(this.textLinkData.linkType===2&&this.textLinkData.appPackageName&&!this.textLinkData.appMinVersion){
        this.$confirm("", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
        return false;
      }
      if(this.textLinkData.linkType===2&&this.textLinkData.appPackageName&&!(this.textLinkData.action||this.textLinkData.appInterfaceClassName||this.textLinkData.url)){
        this.$confirm("actionurl", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
        return false;
      }
      // 
      if(this.btnDialogVisible&&!(this.btnData.appPackageName||this.btnData.cpFastLink||this.btnData.cpH5Link)){
        this.$confirm("", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
        return false;
      }
      if(this.btnDialogVisible&&this.btnData.appPackageName&&!this.btnData.appMinVersion){
        this.$confirm("", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
        return false;
      }
      if(this.btnDialogVisible&&this.btnData.appPackageName&&!(this.btnData.action||this.btnData.appInterfaceClassName||this.btnData.url)){
        this.$confirm("actionurl", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
        return false;
      }
      return nflag&&cflag&&tflag&&bflag&&noflag;
    },
    //
    async draftButton() {
      if (this.dataValidate(true)) {
        if(await this.hasSameData()){
          this.$confirm("tips", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
            center: true
          })
          .then(() => {
            this.prepareEditData(0);
            })
          .catch(() => {
            this.contentData.priority = "";
            this.$message({
              type: "info",
              message: ""
            });
          });
        }else{
          this.prepareEditData(0);
        }
      }
    },
    //
    async hasSameData() {
      let param = {
        tipsId: this.baseData.tipsId?this.baseData.tipsId:undefined,
        startTime: this.contentData.time[0],
        priority: this.contentData.priority,
      }
      return new Promise((resolve) => {
        this.$http
          .post('/scene/judgeStartTimeAndPriority', param)
          .then(response => {
            if (response.data && response.data.code == 0) {
              resolve(response.data.data);
            } else {
              resolve(false);
            }
          })
          .catch(() => {
            resolve(false);
          });
      });
    },
    //
    saveButton() {
      this.prepareEditData(1);
    },
    //
    async prepareEditData(status) {
      let btnParams = {
        linkType: 2,
        appPackageName: this.btnData.appPackageName,
        appInterfaceClassName: this.btnData.appPackageName?this.btnData.appInterfaceClassName:undefined,
        appMinVersion: this.btnData.appPackageName?this.btnData.appMinVersion:undefined,
        action: this.btnData.appPackageName?this.btnData.action:undefined,
        appMinVersion: this.btnData.appPackageName?this.btnData.appMinVersion:undefined,
        url: this.btnData.appPackageName?this.btnData.url:undefined,
        cpFastLink: this.btnData.cpFastLink,
        cpH5Link: this.btnData.cpH5Link,
      }
      let param = {
        tipsId: this.baseData.tipsId,
        status: status,
        versionCode: this.baseData.versionCode,
        name: this.nameData.name,
        startTime: this.contentData.time[0],
        endTime: this.contentData.time[1],
        priority: this.contentData.priority,
        iconUrl: this.contentData.iconUrl,
        displayText: this.contentData.displayText,
        textLink: this.textLinkData,
        buttonName: this.btnDialogVisible?this.btnData.buttonName:'',
        buttonLink: this.btnDialogVisible?btnParams:undefined,
        notice: this.noticeDialogVisible?this.noticeData:undefined,
        imeis: this.imeiList.length>0?this.imeiList.join(';'):undefined
      };
      if(this.isAdd){
        this.addDataXhr(param);
      }else{
        this.editDataXhr(param);
      }

    },
    //xhr
    addDataXhr(param) {
      this.$http
        .post("/scene/addTips", param)
        .then(response => {
          this.uploadLoading = false;
          if (response.data && response.data.code == "0") {
            if (this.stepNumber === 1) {
              this.$message({
                type: "success",
                message: ""
              });
            } else {
              this.stepNumber++;
            }
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("");
            }
          }
        })
        .catch(() => {
          this.uploadLoading = false;
          this.$message.error("");
        });
    },
    // 
    editDataXhr(param) {
      this.$http
        .post("/scene/modifyTips", param)
        .then(response => {
          this.uploadLoading = false;
          if (response.data && response.data.code == "0") {
            if (this.stepNumber === 1) {
              this.$message({
                type: "success",
                message: ""
              });
            } else {
              this.stepNumber++;
            }
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("");
            }
          }
        })
        .catch(() => {
          this.uploadLoading = false;
          this.$message.error("");
        });
    },
    //
    lastStepButton() {
      this.stepNumber--;
    },
    //
    async nextStepButton() {
      if (this.dataValidate(false)) {
        if(await this.hasSameData()){
          this.$confirm("tips", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
            center: true
          })
          .then(() => {
            this.stepNumber++; 
            })
          .catch(() => {
            this.contentData.priority = "";
            this.$message({
              type: "info",
              message: ""
            });
          });
        }else{
          this.stepNumber++; 
        }
      }
    },
    //
    cancelEditButton() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$emit("tips-event", {
            type: "home-page"
          });
        })
        .catch(() => { });
    },
    //xhr
    getDataXhr() {
      let param = {
        tipsId: this.baseData.tipsId,
        versionCode: this.baseData.versionCode
      };
      this.$http
        .post("/scene/getTipsDetail", param)
        .then(response => {
          if (
            response.data &&
            response.data.code == "0" &&
            response.data.data
          ) {
            let data = response.data.data
            this.nameData.name = data.name;
            this.nameData.versionCode = data.versionCode;
            this.contentData.displayText = data.displayText;
            this.contentData.iconUrl = data.iconUrl;
            this.contentData.time = [data.startTime,data.endTime];
            this.contentData.priority = data.priority;
            this.textLinkData = data.textLink;
            this.textLinkData.appMinVersion = this.textLinkData.appMinVersion+'';
            this.noticeData = data.notice?data.notice:{};
            this.noticeDialogVisible = data.notice?true:false;
            this.btnData = data.buttonLink?data.buttonLink:{linkType: 2};
            this.btnData.buttonName = data.buttonName;
            this.btnData.appMinVersion = this.btnData.appMinVersion+'';
            this.btnDialogVisible = data.buttonLink ? true : false;
            this.nowStatus = data.status;
            this.jinyongReason = data.disableReason;
            this.xiajiaReason = data.pulloffReason;
            this.approvalData.approvalTestUserName = data.approvalTestUserName;
            this.approvalData.approvalTestStatus = data.approvalTestStatus;
            this.approvalData.approvalTestReason = data.approvalTestReason;
            this.approvalData.approvalOperationUserName = data.approvalOperationUserName;
            this.approvalData.approvalOperationStatus = data.approvalOperationStatus;
            this.approvalData.approvalOperationReason = data.approvalOperationReason;
            this.contentData = JSON.parse(JSON.stringify(this.contentData))
            this.nameData = JSON.parse(JSON.stringify(this.nameData))
            this.textLinkData = JSON.parse(JSON.stringify(this.textLinkData))
            this.btnData = JSON.parse(JSON.stringify(this.btnData))
            this.noticeData = JSON.parse(JSON.stringify(this.noticeData))
            this.imeiList = data.previewImeis?data.previewImeis.split(';'):[];
            if(!this.btnDialogVisible){
              this.deleteBtn();
            }
            if(data.buttonLink&&!data.buttonLink.appPackageName){
              this.btnData.appMinVersion = "";
            }
            if(data.textLink&&!data.textLink.appPackageName){
              this.textLinkData.appMinVersion = "";
            }
            if(!this.noticeDialogVisible){
              this.deleteNotice();
            }
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //
    getDisappearTime(date, afterDate) {
      let d1 = new Date(date);
      let d2 = new Date(afterDate);
      let t = d2.getTime() - d1.getTime();
      if (t > 0) {
        return Math.round(t / 60000);
      }
    }
  },
  //
  mounted: function () {
    this.isDetail = this.isDetailpage;
    if (this.isDetailpage) {
      this.isEditPage = false;
      this.stepNumber = 2;
    }
    if (typeof this.baseData.tipsId !== 'undefined') {
      this.getDataXhr();
    }
  },
  created: function () {
    // this.getDataXhr();
  }
};
</script>

